<template>
  <div class="abboutus">
    <div class="aboutus-1">
      <div class="select">
        <div @click="select(0)" class="s" :class="[id==0?'s-1-1':'s-1']">
          <h4>公司简介</h4>
        </div>
        <div @click="select(1)" class="s" :class="[id==1?'s-2-1':'s-2']">
          <h4>企业文化</h4>
        </div>
        <div @click="select(2)" class="s" :class="[id==2?'s-3-1':'s-3']">
          <h4>组织架构</h4>
        </div>
      </div>
    </div>
    <div class="show">
      <img src="http://kodeboy.com/static/img/ab_bg_1.d49fadc.png" v-show="this.id==0" alt="">
      <img src="http://kodeboy.com/static/img/ab_bg_2.1f7c486.png" v-show="this.id==1" alt="">
      <img src="http://kodeboy.com/static/img/ab_bg_3.d1d15e5.png" v-show="this.id==2" alt="">
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        show: false,
        id: 0
      }
    },
    methods: {
      select(id) {
        this.id = id;
      }
    }

  }
</script>

<style scoped>
  .aboutus-1 {
    background: url(http://kodeboy.com/static/img/about_bg.bee6a6b.jpg);
    width: 100%;
    height: 400px;
  }

  .select {
    margin: auto;
    width: 60%;
    height: 400px;
    display: flex;
    justify-content: space-around;
  }

  .s {
    width: 20%;
    margin: 150px 10px;
    cursor: pointer;
  }

  .s h4 {
    color: white;
    margin-top: 80px;
    margin-left: 10px;
  }

  .s-1-1 {
    background: url(data:image/png;base64,);

    background-repeat: no-repeat;
  }

  .s-1 {
    background: url(data:image/png;base64,);
    background-repeat: no-repeat;
  }

  .s-2-1 {
    background: url(data:image/png;base64,);
    background-repeat: no-repeat;
  }

  .s-2 {
    background: url(data:image/png;base64,);
    background-repeat: no-repeat;
  }

  .s-3-1 {
    background: url(data:image/png;base64,);
    background-repeat: no-repeat;
  }

  .s-3 {
    background: url(data:image/png;base64,);
    background-repeat: no-repeat;
  }

  .show {
    width: 100%;
    margin: 80px auto;
  }

  .show img {
    display: block;
    margin: auto;
  }
</style>



