<template>
  <div class="product">
    <div class="product-1">
      <div class="about">
        <h1>Product Center</h1>
        <h3>产品中心</h3>
      </div>
    </div>
    <div class="product-2">
      <div class="selcet">
        <span :class="[data==1?'actived':'']" @click="select(1)">产品应用</span>
        <span :class="[data==2?'actived':'']" @click="select(2)">代理品牌</span>
        <div class="line">
          <div :class="[data==1?'first':'sencond']" style="margin-left:20px ; width: 64px; height: 4px; background-color: #409eff; z-index: 2;"></div>
        </div>
      </div>
      <div v-if="this.data==2" class="contain">
        <div class="c-1">
          <div class="c-1-f">

          </div>
          <div class="c-1-s">
            代理产品覆盖汽车，手机，家电等众多应用领域，产品质量可靠。目前已经与国内200余家终端厂商达成合作，提供长期的产品支持和服务。合作企业有格力，海尔，惠而浦，美菱，小米，华为，江淮汽车等国内知名生产商
          </div>
        </div>
        <div class="c-2">
          <div class="c-2-f">
            <ul>
              <li @click="to(1)" :class="[dat==1?'isHover el-tabs__item':'el-tabs__item']">UNIOHM(厚声)</li>
              <li @click="to(2)" :class="[dat==2?'isHover el-tabs__item':'el-tabs__item']">SAMSUNG(三星)</li>
              <li @click="to(3)" :class="[dat==3?'isHover el-tabs__item':'el-tabs__item']">CHILISIN(齐力新)</li>
              <li @click="to(4)" :class="[dat==4?'isHover el-tabs__item':'el-tabs__item']">WAISIN(华科)</li>
              <li @click="to(5)" :class="[dat==5?'isHover el-tabs__item':'el-tabs__item']">TKD(泰晶)</li>
              <li @click="to(6)" :class="[dat==6?'isHover el-tabs__item':'el-tabs__item']">EPSON(爱普生)</li>
              <li @click="to(7)" :class="[dat==7?'isHover el-tabs__item':'el-tabs__item']">VIKING(光颉)</li>
              <li @click="to(8)" :class="[dat==8?'isHover el-tabs__item':'el-tabs__item']">SAMSON(萨姆森)</li>
              <li @click="to(9)" :class="[dat==9?'isHover el-tabs__item':'el-tabs__item']">FARATRONIC(法拉)</li>
              <li @click="to(10)" :class="[dat==10?'isHover el-tabs__item':'el-tabs__item']">YAGEO(国巨)</li>
            </ul>
          </div>
          <div class="c-2-s">
            <p class="des">待管理员完善产品数据</p>
          </div>
        </div>
      </div>
      <div v-if="this.data==1" class="contain">
          <div class="c-1">
            <div class="cc">

            </div>
            <div class="c-1-s">
              代理国内外知名电子元器件品牌，质量保证。目前已经与国内200余家终端厂商达成合作，提供长期的产品支持和服务。分立器件供应链专业代理服务商。TDK 国巨 三星 华科 兆易 爱普科斯TE RF360 TXC 君耀 顺络 大毅 文善 华科 乐山 TST等
            </div>
          </div>
          <div class="c-2">
            <div class="c-2-f ">
              <ul>
                <li @click="to(1)" :class="[dat==1?'isHover el-tabs__item':'el-tabs__item']">贴片电容</li>
                <li @click="to(2)" :class="[dat==2?'isHover el-tabs__item':'el-tabs__item']">二三级管</li>
                <li @click="to(3)" :class="[dat==3?'isHover el-tabs__item':'el-tabs__item']">电压瓷片</li>
                <li @click="to(4)" :class="[dat==4?'isHover el-tabs__item':'el-tabs__item']">陶振</li>
                <li @click="to(5)" :class="[dat==5?'isHover el-tabs__item':'el-tabs__item']">锂电池</li>
                <li @click="to(6)" :class="[dat==6?'isHover el-tabs__item':'el-tabs__item']">插件电阻</li>
                <li @click="to(7)" :class="[dat==7?'isHover el-tabs__item':'el-tabs__item']"汽车器件</li>
                <li @click="to(8)" :class="[dat==8?'isHover el-tabs__item':'el-tabs__item']">热敏电阻</li>
                <li @click="to(9)" :class="[dat==9?'isHover el-tabs__item':'el-tabs__item']">电容</li>
                <li @click="to(10)" :class="[dat==10?'isHover el-tabs__item':'el-tabs__item']">滤波器</li>
                <li @click="to(11)" :class="[dat==11?'isHover el-tabs__item':'el-tabs__item']">磁材</li>
              </ul>
            </div>
            <div class="c-2-s">
              <p  class="des">待管理员完善产品数据</p>
            </div>
          </div>
        </div>

    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        data: 1,
        dat:1
      }
    },
    methods: {
      select(id) {
        this.data = id;
        this.dat =1
      },
      to(index){
       this.dat =index
      }
    }
  }
</script>

<style scoped>
  .product {
    width: 100%;
    height: 100%;
  }

  .product-1 {
    width: 100%;
    height: 100%;
    margin: auto;
    background: url(http://kodeboy.com//static/img/proCent1.9573eac.jpg);
  }

  .about {
    font-size: 18px;
    color: white;
    width: 300px;
    height: 200px;
    margin: auto;
    padding-top: 200px;
    text-align: center;
  }

  .product-2 {
    width: 1200px;
    height: 700px;
    margin: auto;
    margin-top: 30px;
  }

  .product-2 span {
    display: inline-block;
    padding-bottom: 8px;
    margin-left: 20px;
  }

  .line {
    width: 100%;
    height: 2px;
    border-bottom: 2px solid beige;
    margin-top: -4px;
  }

  .actived {
    color: #409eff;
  }

  .first {
    transition: .6s;
    transform: translate(0px);
  }

  .sencond {
    transition: .6s;
    transform: translate(90px);
  }

  .contain {
    width: 100%;
    height: 500px;
    margin-top: 30px;
  }

  .left {
    width: 240px;
    height: 100%;
    border: 1px solid skyblue;
  }

  .c-1 {
    width: 100%;
    height: 80px;
    display: flex;
  }
.cc{
  width: 250px;
  height: 100%;
  background: url(data:image/jpeg;base64,);
}
  .c-1-f {
    width: 250px;
    height: 100%;
    background: url(data:image/jpeg;base64,);
  }

  .c-1-s {
    width: calc(100% - 260px);
    height: 100%;
    margin-left: 10px;
    border-radius: 4px;
    background-color: #D3DCE6;
    line-height: 26px;
    text-indent: 2em;
  }

  .c-2 {
    width: 100%;
    height: calc(100% - 100px);
    display: flex;
  }

  .c-2-f {
    width: 250px;
    height: 100%;
  }

  .c-2 ul li {
    border: 1px solid lightblue;
    display: block;
  }
.isHover{
  background-color: #409EFF;
  color: white;
}
.c-2-s{
  width: calc(100% - 250px );
}
.des{
  text-align: center;
  padding: 80px;
}
</style>



