<template>
  <div class="header">
    <div class="img">
      <img src="../assets/download.png" style="background-color:#545c64 ;" />
    </div>
      <div>
        <el-menu :default-active="activeIndex" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b" class="el-menu-demo" mode="horizontal">
        <el-menu-item index="1" @click="$router.push('/home')">首页</el-menu-item>
        <el-menu-item index="2" @click="$router.push('/aboutus')">关于我们</el-menu-item>
        <el-menu-item index="3" @click="$router.push('/product')">产品中心</el-menu-item>
        <el-menu-item index="4" @click="$router.push('/guest')">客户案例</el-menu-item>
        <el-menu-item index="5" @click="$router.push('/news')">新闻动态</el-menu-item>
        <el-menu-item index="6" @click="$router.push('/joinus')">加入我们</el-menu-item>
        <el-menu-item index="7" @click="$router.push('/callus')">联系我们</el-menu-item>
      </el-menu>
      </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        activeIndex: '1',
       };
    }
  }
</script>

<style scoped>
.header{
  height:60px;
  display: flex;
  justify-content: space-around;
  background-color: #545c64;
}
.el-menu.el-menu--horizontal {
    border-bottom: 0 !important;
    }
.img{
  padding: 20px;
}
</style>



